/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/

body,
button,
input,
select,
textarea {
	color: $color__text-main;
	font-family: $font__main;
	@include font-size(1);
	line-height: $font__line-height-body;
}

/* Headings */

h1, h2, h3, h4, h5, h6 {
	clear: both;
}

h2 {
	clear: both;
    font-family: $font__nove;
    letter-spacing: 2px;
    text-transform: uppercase;
}
h3 {
	font-size: 1.4em;
	margin: 0 auto;
	font-family: $font__nove;
	letter-spacing: 2px;
}


/* Copy */

p {
	margin-bottom: $base__space-unit;
}

